import url from './url';
import axios from 'axios';
import { post, get, put, deleted } from './http'

/**
 * 管理员登录
 * @param {用户名：admin} username 
 * @param {密码：admin} password 
 */
export function loginAsAdmin(username,password) {
    const admin = {
        Username: username || "admin",
        Password: password || "admin"
    }
    return post({
        url: url.GET_AUTH,
        method: 'post',
        data: admin
    })
}
/**
 * token的存取和删除
 */
export function setToken(key,value) {
    localStorage.setItem(key,JSON.stringify(value));
}
export function getToken(key) {
    return localStorage.getItem(key) || 
    'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJleHAiOjE1ODAwMjQzMzUsImlkIjoiYWRtaW4iLCJvcmlnX2lhdCI6MTU3OTQxOTUzNX0.mQzE-bFxiNeNEPGqsG8xqxhq_tEsuTkukx8v-pN0rzs';
}
export function removeToken(key) {
    localStorage.removeItem(key);
}
/**
 * 获取全部历史事件
 */
export function getAllStories() {
    console.log('触发获取历史事件函数')
    return get({
        url: url.STORY,
        method: 'get'
    })
}
export function timestampFormat(timestamp,format) {
    y = timestamp.getFullYear(),
    m = ("0" + (timestamp.getMonth() + 1)).slice(-2),
    d = ("0" + timestamp.getDate()).slice(-2);
    return y + "-" + m + "-" + d + " " + timestamp.toTimeString().substr(0, 8);
}
/**
 * 获取指定id历史事件
 * @param {事件id，2有效，6为空} data 
 */
export function getOneStory(data) {
    return get({
        url:`${url.STORY}/${data}`,
        method: 'get'
    })
}
/**
 * 添加事件
 * @param {事件时间} time 
 * @param {事件名称} name 
 * @param {事件描述} des 
 */
export function addOneStory(time,name,des) {
    const data = {
        time: time,
        name: name,
        storyDescribe: des
    }
    var token = getToken('token');
    return post({
        url: url.STORY,
        method: 'post',
        data: data,
        headers: {
            token: token
        }
    })
}
/**
 * 修改事件
 * @param {事件发生的事件} time 
 * @param {事件名称} name 
 * @param {事件描述} des 
 * @param {事件id} id 
 */
export function changeStory(time,name,des,id) {
    const data ={
        time: time,
        name: name,
        storyDescribe:des
    }
    var token = getToken('token');
    return put({
        url: `${url.STORY}/${id}`,
        method: 'put',
        data: data,
        headers: {
            token: token
        }
    })
}
/**
 * 删除事件
 * @param {事件id} id 
 */
export function deleteStory(id) {
    var token = getToken('token');
    return deleted({
        url: `${url.STORY}/${id}`,
        method: 'delete',
        headers: {
            token:token
        }
    })
}


