import Vue from 'vue'
import Router from 'vue-router'
import Production from '../view/Production'
import HomePage from '../view/HomePage'
import Corporation from '../view/Corporation'
import Connection from '../view/Connection'
import Member from '../view/Member'
import Management from '../view/Management'
Vue.use(Router)

const routes = [
  {
    path: '/',
    name: 'homepage',
    component: HomePage,
    meta: {
      requireAuth: false,
    }
  },
  {
    path: '/homepage',
    name: 'homepage',
    component: HomePage,
    meta: {
      requireAuth: false,
    }
  },
  {
    path: '/production',
    name: 'production',
    component: Production,
    meta: {
      requireAuth: false,
    }
  },
  {
    path: '/corporation',
    name: 'corporation',
    component: Corporation,
    meta: {
      requireAuth: false,
    }
  },
  {
    path: '/member',
    name: 'member',
    component: Member,
    meta: {
      requireAuth: false,
    }
  },
  {
    path: '/connection',
    name: 'connection',
    component: Connection,
    meta: {
      requireAuth: false
    }
  },
  {
    path: '/management',
    name: 'management',
    component: Management,
    meta: {
      requireAuth: true
    }
  },
  {
    path: '*',
    redirect: '/homepage'
  }
 ];

const router = new Router({
  mode:"hash",
  routes:routes
})

export default router

