<template >
  <div id="app">
    <a-layout class="fullHeight">
      <!-- pc端左边的空白填充 -->
      <a-layout-sider breakpoint="lg" collapsedWidth="0.1" class="sideGeryBlock">
        <div style="background:white;height:64px"></div>
      </a-layout-sider>
      <!-- pc端可见的导航栏 -->
      <a-layout-sider
        breakpoint="md"
        collapsedWidth="0"
        @collapse="onCollapse"
        @breakpoint="onBreakpoint"
        style="background:white"
      >
        <br />
        <br />
        <br />
        <!-- 菜单 -->
        <a-menu 
          theme="light" 
          mode="inline" 
          :defaultSelectedKeys="['主页']"
          @openChange="onOpenChange"
          :openKeys="openKeys">
          <!-- 子菜单 -->
          <a-sub-menu v-for="(value, name, index) of menu" :key="value.name">
            <span slot="title"  @click="routerLink(name)">
              <a-icon :type="icon[index]" />
              <div style="border:1px solid red;display:inline-block;width:110px">{{value.name}}</div>
            </span>
            <!-- 菜单项 -->
            <a-menu-item v-for="(subvalue, subname, subindex) of value.detail" :key="subvalue">
              <a :href="subname">{{subvalue}}</a>
            </a-menu-item>
          </a-sub-menu>
        </a-menu>
        <!-- 底下固定的图标 -->
        <a-row>
          <a-col :span="12">
            <img src="./assets/img/wei.png" alt class="wid24 roundLogo" />
          </a-col>
          <a-col :span="12"></a-col>
        </a-row>
      </a-layout-sider>
      <!-- 主体内容 -->
      <a-layout>
        <a-layout-content>
          <div class="topHeader">
            <img src="./assets/img/wizzStudio.jpg" class="topLogo" alt />
          </div>
          <div class="mainBody">
            <router-view />
          </div>
        </a-layout-content>
      </a-layout>
      <!-- pc端右侧的空白填充 -->
      <a-layout-sider breakpoint="lg" collapsedWidth="0.1" class="sideGeryBlock"></a-layout-sider>
    </a-layout>
  </div>
</template>

<script>
import "./assets/style/public.css";
import router from "./router/index"
export default {
  name: "App",
  data: function() {
    return {
      rootSubmenuKeys: ['主页', '产品', '合作与导师', '成员（部门）', '联系我们', '后台管理'],
      openKeys: ['主页'],
      //menu每个对象的name用于路由跳转，detail的name用于锚点定位
      menu: {
        "homepage":{
          name: "主页",
          detail: {
            "#firstPage": "首页",
            "#introduction": "介绍",
            "#culture": "文化",
            "#history": "历史",
            "#atmospher": "氛围建设",
            "#member": "成员去向"
          }
        },
        "production": {
          name: "产品",
          detail: {
            "#playGame": "约游戏",
            "#rankOfLan": "编程语言排行",
            "#model": "模板"
          }
        },
        "corporation": {
          name: "合作与导师",
          detail: {
            "#teacher": "导师"
          }
        },
        "member": {
          name: "成员（部门）",
          detail: {
            "#memberInfo": "成员信息"
          }
        },
        "connection": {
          name: "联系我们",
          detail: {
            "#connect": "联系方式"
          }
        },
        "management": {
          name: "后台管理",
          detail: {
            "#historyManage": "历史管理",
            "#productionManage": "产品管理",
            "#memberManage": "成员管理"
          }
        }
      },
      icon: ["appstore", "dropbox", "slack", "idcard", "link", "cloud"]
    };
  },

  methods: {
    onCollapse(collapsed, type) {
      console.log(collapsed, type);
    },
    onBreakpoint(broken) {
      console.log(broken);
    },
    onOpenChange(openKeys) {
      console.log("闭合其他部分")
      const latestOpenKey = openKeys.find(key => this.openKeys.indexOf(key) === -1);
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys;
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    },
    routerLink: function(data) {
      console.log(data);
      console.log("路由跳转");
      router.push({name: data})
    }
  }
};
</script>

<style scope>
.topLogo {
  top: 0px;
  height: 64px;
  display: block;
  width: 200px;
  margin: 0px 40px;
}
.topHeader {
  background: white;
  position: absolute;
  height: 64px;
  width: 100%;
  z-index: 1;
}
.mainBody {
  margin: 74px 10px 10px;
  padding: 10px;
  border-radius: 10px;
  min-height: 90%;
  background: white;
}
.sideGeryBlock {
  background: #f0f2f5;
}
.roundLogo {
  position: fixed;
  bottom: 0;
  width: 50px;
  z-index: 9999;
}
</style>



